<template>
  <b-modal
    id="modal_asignar_periodo"
    ok-only
    size="md"
    class="p-5"
    header-bg-variant="primary"
    hide-footer
  >
    <template
      #modal-header
    >
      <div class="text-white">
        <strong class="float-left">
          Asignar periodo beca
        </strong>
      </div>

    </template>
    <!-- BODY -->
    <validation-observer
      #default="{ handleSubmit }"
      ref="refFormObserver"
    >
      <!-- Form -->
      <b-form
        class="px-2"
        @submit.prevent="handleSubmit(onSubmit)"
        @reset.prevent="resetForm"
      >
        <b-row>
          <b-col
            lg="8"
            md="7"
            sm="6"
            xs="12"
          >
            <feather-icon
              icon="UsersIcon"
              size="16"
              class="mr-2"
            />
            <strong style="">{{ becarios.length }} becarios seleccionados</strong>
          </b-col>

        </b-row>
        <br>
        <!-- Fecha inicio -->
        <b-row>
          <b-col md="12">
            <validation-provider
              #default="validationContext"
              name="fecha_inicio"
              rules="required"
            >
              <b-form-group>
                <label>Fecha Inicio</label>
                <b-form-datepicker
                  v-model="becario.fecha_inicio"
                  class="form-control"
                  :config="{ enableTime: true,dateFormat: 'Y-m-d'}"
                  :state="getValidationState(validationContext)"
                  @input="() => {becario.fecha_fin = null}"
                />
                <b-form-invalid-feedback
                  :state="getValidationState(validationContext)"
                >
                  Complete el campo fecha inicio
                </b-form-invalid-feedback></b-form-group>
            </validation-provider>
          </b-col>
          <b-col md="12">
            <validation-provider
              #default="validationContext"
              name="fecha_fin"
              rules="required"
            >
              <b-form-group>
                <label>Fecha Fin (probable)</label>
                <b-form-datepicker
                  v-model="becario.fecha_fin"
                  class="form-control"
                  :state="getValidationState(validationContext)"
                  :config="{ enableTime: true,dateFormat: 'Y-m-d', disable:[{from:'1900-01-01',to:becario.fecha_inicio == null ? null : becario.fecha_inicio}]}"
                />
                <b-form-invalid-feedback
                  :state="getValidationState(validationContext)"
                >
                  Complete el campo fecha fin

                </b-form-invalid-feedback></b-form-group>
            </validation-provider>
          </b-col>
        </b-row>

        <b-row v-if="servicio[0] == 15">
          <b-col cols="12">

            <validation-provider
              #default="validationContext"
              name="ubicacion_bolsa"
              rules="required"
            >
              <b-form-group
                label="rea de trabajo"
                label-for="ubicacion_bolsa"
              >
                <b-form-input
                  v-model="becario.ubicacion_bolsa"
                />
                <b-form-invalid-feedback
                  :state="getValidationState(validationContext)"
                >
                  Complete el campo area de trabajo
                </b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>
          </b-col>
        </b-row>

        <!-- Form Actions -->
        <div class="d-flex justify-content-between mt-3 mb-1">
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            type="button"
            variant="outline-danger"
            size="sm"
            @click="$bvModal.hide('modal_asignar_periodo')"
          >
            <feather-icon
              icon="XIcon"
              size="14"
            />
            Cancelar
          </b-button>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            :variant="`outline-primary`"
            class="mr-2"
            type="submit"
          >
            Guardar
            <feather-icon
              icon="SaveIcon"
              size="14"
            />
          </b-button>
        </div>
      </b-form>
    </validation-observer>
  </b-modal>
</template>

<script>

import {
  BButton, BModal, BForm, BFormGroup, BFormInvalidFeedback, BCol, BRow, BFormDatepicker, BFormInput,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'

export default {
  components: {

    BButton,
    BCol,
    BRow,
    BModal,
    BForm,
    BFormGroup,
    BFormInvalidFeedback,
    BFormDatepicker,
    ValidationObserver,
    ValidationProvider,
    BFormInput,
  },
  directives: {
    Ripple,
  },
  props: {
    servicio: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      required,
      becario: {},
      becarios: [],
      es_retiro: false,
    }
  },
  created() {
    this.$parent.$on('asignarPeriodo', this.asignarPeriodo)
  },
  methods: {
    asignarPeriodo(becariosSelected) {
      console.log('DATA POSTULANTE:', becariosSelected)
      this.becarios = becariosSelected
      if (!this.becarios.length) {
        this.$toast({
          component: ToastificationContent,
          position: 'top-right',
          props: {
            title: 'Advertencia!',
            text: 'No existe becarios seleccionados',
            icon: 'AlertCircleIcon',
            variant: 'warning',
          },
        })
        return
      }
      this.$bvModal.show('modal_asignar_periodo')
    },
    onSubmit() {
      const data = this.becario
      data.becarios = this.becarios
      data.servicios_id = this.servicio
      data.es_retiro = this.es_retiro

      this.$emit('onSubmitmModalAsignar', data)
    },
  },
  setup() {
    const {
      refFormObserver,
      getValidationState,
      resetForm,
    } = formValidation()

    return {
      refFormObserver,
      getValidationState,
      resetForm,
    }
  },
}
</script>

<style lang="scss" scoped>
</style>
